.home-container {
  width: 100%;
}
.home-container img {
  width: 100%;
  object-fit: cover;
  height: 70vh;
}

.home-section {
  width: 90%;
  margin: 2rem auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}
.left-section {
  width: 25%;
  padding: 1rem 0;
  border: 2px solid rgb(146, 145, 145);
  border-radius: 2rem;
  background-color: #f1f1f1;
}

.search-form {
  width: 100%;
  text-align: center;
}

.home-in {
  width: 90%;
  padding: 1rem;
  border: none;
  border: 2px solid #e1e1e1;
  border-radius: 1rem;
  outline: none;
  font-size: 1.8rem;
}
.home-btn {
  text-align: center;
  max-width: fit-content;
  margin: 1rem auto;
  border: none;
  padding: 1rem 1rem;
  background-color: #035afc;
  color: #fff;
  border-radius: 1rem;
  text-transform: capitalize;
  font-size: 1.5rem;
}
.checkbox {
  width: 90%;
  background-color: #fff;
  margin: 0.5rem auto;
  border-radius: 1rem;
  border: 2px solid #6d6b6b;
  padding: 0.5rem 2rem;
  text-transform: capitalize;
}
.radiobox {
  width: 100%;
}
.radio {
  width: 90%;
  background-color: #fff;
  margin: 0.5rem auto;
  border-radius: 1rem;
  border: 2px solid #6d6b6b;
  padding: 0.5rem 2rem;
  text-transform: capitalize;
}

.right-section {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: flex-start;
  border-radius: 2rem;
  background-color: #f1f1f1;
  border: 2px solid rgb(146, 145, 145);
}

.card {
  width: 30%;
  margin: 1rem;
  padding: 1rem 1rem;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  border-radius: 2rem;
  box-shadow: 5px 5px 10px #494949;
  transition: 1s ease;
}
.card:hover {
  transform: scale(1.02);
}
.card img {
  object-fit: contain;
  width: 80%;
  height: 150px;
  border: 2px solid #e1e3e1;
  border-radius: 2rem;
}
.card h2 {
  font-size: 1.6rem;
  text-transform: capitalize;
  padding: 0.5rem 0;
  color: #0c0c0c;
}
.price {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* color: rgb(109, 109, 108); */
  font-size: 1.4rem;
  text-transform: capitalize;
}
.flex p {
  font-size: 1.4rem;
  color: #000;
}

.home-card-section {
  width: 90%;
  margin: 2rem auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}
.home-card {
  width: 10%;
  height: 130px;
  margin: 1rem;
  padding: 2rem 0;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  background-color: #e1e1e1;
  border-radius: 50%;
  border: 2px solid #52fd30;
  box-shadow: 5px 5px 30px #494949;
  transition: 1s ease;
  color: #0516ff;
}
.home-card:hover {
  transform: scale(1.1);
}
.home-card img {
  object-fit: contain;
  width: 80%;
  height: 80%;
}
.home-card a h2 {
  font-size: 1.6rem;
  text-transform: capitalize;
}
/* ABout page css */

.about-container {
  width: 100%;
  position: relative;
  text-align: center;
}
.about-container img {
  width: 100%;
  height: 40vh;
  object-fit: cover;
}
.about-container h2 {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-40%, -50%);
}

.about-us {
  width: 90%;
  margin: 1rem auto;
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
}
.left-about-us {
  margin: 2rem auto;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 40%;
}
.left-about-us img {
  width: 70%;
  border-radius: 2rem;
  box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px,
    rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px,
    rgba(240, 46, 170, 0.05) 25px 25px;
}
.right-about-us {
  border: 2px solid #e7e7e7;
  padding: 0.5rem 2rem;
  width: 40%;
  border-radius: 2rem;
  margin: 2rem auto;
  box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px,
    rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
}
.right-about-us p {
  font-size: 1.6rem;
  word-spacing: 0.5rem;
  color: #494949;
  line-height: 30px;
  padding: 2rem 0;
  text-align: justify;
}
.right-about-us marquee {
  font-size: 1.6rem;
  border-radius: 1rem;
  background-color: #000;
  color: #fff;
  word-spacing: 0.5rem;
  padding: 1rem 0;
}

/* Search page css */
.flex-section {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 90%;
  margin: 2rem auto;
}
/* cart page */
.card-info {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  padding: 1rem;
  font-size: 2rem;
  color: red;
  margin: 0 auto;
  border-top: 2px solid #a3a1a1;
  text-transform: capitalize;
}
.payment {
  font-size: 2rem;
  color: #000;
  text-transform: capitalize;
  text-align: center;
}

/* page not found css */

.pnf {
  width: 100%;
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pnf img {
  width: 30%;
  border-radius: 2rem;
  box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px,
    rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px,
    rgba(240, 46, 170, 0.05) 25px 25px;
  border: 2px solid #bbbaba;
}
@media only screen and (max-width: 1199px) {
  .home-section {
    width: 90%;
    gap: 1rem;
    margin: 0px auto;
  }
  .left-section {
    width: 35%;
  }
  .right-section {
    width: 60%;
  }
  .card {
    width: 40%;
  }
  .home-card {
    width: 15%;
  }
}
@media only screen and (max-width: 999px) {
  .home-section {
    width: 90%;
    gap: 1rem;
    margin: 0px auto;
  }
  .left-section {
    width: 45%;
  }
  .right-section {
    width: 50%;
  }
  .card {
    width: 80%;
  }
  .home-card {
    width: 18%;
  }
  /* Search page css */
  .flex-section {
    width: 50%;
  }
}
@media only screen and (max-width: 799px) {
  .home-section {
    width: 95%;
    gap: 1rem;
    margin: 0px auto;
  }
  .left-section {
    width: 43%;
  }
  .right-section {
    width: 55%;
  }
  .card {
    width: 100%;
  }
  .home-card {
    width: 22%;
    margin: 2rem;
  }
  /* ABout page css */
  .about-us {
    flex-direction: column;
  }
  .left-about-us {
    width: 80%;
  }
  .left-about-us img {
    width: 90%;
  }
  .right-about-us {
    width: 90%;
  }
  /* Search page css */
  .flex-section {
    width: 50%;
  }
  /* page not found */
  .pnf img {
    width: 50%;
  }
}
@media only screen and (max-width: 599px) {
  .home-section {
    width: 90%;
    gap: 1rem;
    flex-direction: column;
    margin: 0px auto;
  }
  .left-section {
    width: 100%;
  }
  .right-section {
    width: 100%;
  }
  .card {
    width: 45%;
  }
  .home-card {
    width: 30%;
  }
  /* ABout page css */
  .about-us {
    flex-direction: column;
  }
  .left-about-us {
    width: 90%;
  }
  .left-about-us img {
    width: 90%;
  }
  .right-about-us {
    width: 90%;
  }
  /* Search page css */
  .flex-section {
    width: 100%;
  }
  /* page not found */
  .pnf img {
    width: 70%;
  }
}
