.header-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 6rem;
  padding: 0 3rem;
  position: sticky;
  top: 0;
  left: 0;
  background: linear-gradient(to right, #a8f7b2, #fcfc89);
  z-index: 99;
  margin: 0 0 1rem 0;
  box-shadow: 0px 8px 6px -9px rgb(37, 37, 37);
}
.logo {
  font-size: 2.5rem;
  color: #035afc;
  text-decoration: none;
  font-weight: 800;
}
.nav {
  gap: 3rem;
  height: 6rem;
  justify-content: flex-end;
  align-items: center;
  display: flex;
  z-index: -1;
}
.hemburger {
  display: none;
}

.nav li {
  padding: 1rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav-link {
  font-size: 1.8rem;
  color: #ff0000;
  font-weight: 600;

  transition: 1s ease;
}
.nav-link:hover {
  transform: scale(1.1);
}
.nav-link.active {
  font-size: 1.8rem;
  font-weight: 600;
  color: #00ff00 !important;
}

.nav-link sup {
  margin-left: 0.5rem;
  width: 4rem;
  height: 4rem;
  padding: 0.2rem 0.5rem;
  border-radius: 50%;
  background-color: red;
  color: #fff;
  font-weight: 700;
}
.dropdown-menu {
  display: none;
}
.nav-item-dropdown {
  position: relative;
}
.nav-item-dropdown:hover .dropdown-menu {
  display: block;
  position: absolute;
  top: 4rem;
  left: 0rem;
  width: 15rem;
  padding: 1rem 2rem;
  text-align: center;
  border-radius: 1rem;
  box-shadow: 0px 8px 6px -9px rgb(37, 37, 37);
  background: linear-gradient(to right, #a8f7b2, #fcfc89);
}
@media only screen and (max-width: 799px) {
  .header-container {
    width: 100%;
    height: 7rem;
  }
  .nav {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: absolute;
    top: -100vh;
    left: 0px;
    transition: 1.5s ease;
    background-color: #f1f1f1;
    background: linear-gradient(to right, #a8f7b2, #fcfc89);
  }

  .nav.show {
    top: 0%;
  }
  .nav a {
    padding: 1rem 0px;
    font-size: 1.6rem;
  }
  .hemburger {
    display: initial;
  }
}
