@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  list-style: none;
  text-decoration: none;
}
html {
  font-size: 62.5%;
  overflow-x: hidden;
}
body {
  overflow-x: hidden;
}

.primary-heading {
  width: fit-content;
  margin: 2rem auto;
  margin-bottom: 0;
  font-size: 3rem;
  font-weight: 600;
  padding: 0.5rem;
  text-transform: capitalize;
  color: #ff00aa;
}

.primary-para {
  width: fit-content;
  margin: 0rem auto;
  margin-bottom: 2rem;
  font-size: 1.8rem;
  padding: 1rem;
  text-transform: capitalize;
  color: rgb(0, 0, 0);
}

/*  */
.red {
  background-color: red !important;
}

::placeholder {
  color: #13049c;
  font-size: 1.5rem;
}
.btn-box {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 1.4rem;
  text-transform: capitalize;
}

