.auth-container {
  width: 100%;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f2f2;
}
.auth-section {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0rem auto;
  border-radius: 2rem;
}
.auth-image {
  width: 50%;
  margin: 2rem auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.auth-image img {
  width: 65%;
  border-radius: 2rem;
  box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px,
    rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px,
    rgba(240, 46, 170, 0.05) 25px 25px;
}
.auth-form-section {
  border: 2px solid #e1e1e1;
  margin: 0rem auto;
  padding: 1rem 0 2rem 0;
  border-radius: 2rem;
  width: 40%;
  box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px,
    rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
}

.auth-form {
  width: 80%;
  margin: 0px auto;
}

.auth-input {
  width: 100%;
  margin: 1rem auto;
}
.auth-input label {
  font-size: 1.5rem;
  width: 100%;
}
.auth-in {
  padding: 1rem;
  width: 100%;
  border: none;
  border: 2px solid #e1e1e1;
  border-radius: 1rem;
  outline: none;
  font-size: 1.8rem;
}

.auth-btn {
  text-align: center;
  max-width: fit-content;
  margin: 1rem auto;
  border: none;
  padding: 1rem 2rem;
  background-color: #035afc;
  color: #fff;
  border-radius: 1rem;
  text-transform: capitalize;
  font-size: 1.5rem;
}
.auth-btn:hover {
  background-color: #6197fc;
}

.red-btn {
  background-color: #ff0000;
}
.red-btn:hover {
  background-color: #f74141;
}
@media only screen and (max-width: 1199px) {
  .auth-image {
    display: none;
  }
  .auth-section {
    flex-direction: column;
  }
  .auth-form-section {
    width: 40%;
    margin: 0 auto;
  }
}
@media only screen and (max-width: 999px) {
  .auth-image {
    display: none;
  }
  .auth-section {
    flex-direction: column;
  }
  .auth-form-section {
    width: 60%;
    margin: 0 auto;
  }
}
@media only screen and (max-width: 799px) {
  .auth-image {
    display: none;
  }
  .auth-section {
    flex-direction: column;
  }
  .auth-form-section {
    width: 80%;
    margin: 0 auto;
    padding: 2rem 0;
  }
}
@media only screen and (max-width: 559px) {
  .auth-image {
    display: none;
  }

  .auth-section {
    flex-direction: column;
  }
  .auth-form-section {
    width: 100%;
    padding: 2rem 0;
  }
}
