.admin-container {
  width: 100%;
}
.admin-nav {
  width: 90%;
  margin: 0px auto;
  padding: 0rem;
}
.admin-ul {
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 2rem 0;
  background-color: #fff;
  border-radius: 1rem;
  text-transform: capitalize;
  box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px,
    rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px,
    rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
}
/* admin dash board css */
.admin-left-section {
  width: 90%;
  margin: 2rem auto;
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  justify-content: space-between;
}
.detail-image {
  width: 100%;
  margin: 0rem auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.detail-image img {
  width: 90%;
  border-radius: 2rem;
  box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px,
    rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px,
    rgba(240, 46, 170, 0.05) 25px 25px;
}
.admin-right-section {
  width: 90%;
  margin: 0rem auto;
  gap: 1rem;
  border: 2px solid #d1d1d1;
  border-radius: 2rem;
  padding: 1rem 0 2rem 0;
  box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px,
    rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
}

.admin-right-section-details {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}
.admin-right-section-details img {
  width: 50%;
  margin: 0 auto;
  text-align: center;
}
.admin-right-section-details h2 {
  font-style: 1.6rem;
  margin: 2rem 0;
  color: blue;
  font-weight: 600;
}
.admin-right-section-details h2 span {
  font-style: 1.4rem;
  margin: 1rem;
  color: red;
  font-weight: 500;
}
/* admin dash board css ends */

/* create catergory */
.admin-section {
  width: 100%;
}
.upload-img {
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px auto;
  object-fit: contain;
}
.admin-form-section {
  border: 2px solid #e1e1e1;
  margin: 0rem auto;
  padding: 1rem 0 2rem 0;
  border-radius: 2rem;
  width: 50%;
  box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px,
    rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
}
.admin-form {
  width: 100%;
  margin: 0px auto;
}
.admin-input {
  width: 50%;
  margin: 1rem auto;
}
.admin-input label {
  font-size: 1.5rem;
  width: 100%;
  text-transform: capitalize;
}
.admin-in {
  padding: 1rem;
  width: 100%;
  border: none;
  border: 2px solid #e1e1e1;
  border-radius: 1rem;
  outline: none;
  font-size: 1.8rem;
}
.admin-btn {
  text-align: center;
  max-width: fit-content;
  margin: 1rem auto;
  border: none;
  padding: 1rem 2rem;
  background-color: #035afc;
  color: #fff;
  border-radius: 1rem;
  text-transform: capitalize;
  font-size: 1.5rem;
}

/* product-page -css */
.product-container {
  width: 90%;
  border: 2px solid #d8d5d5;
  border-radius: 2rem;
  box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px,
    rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
  margin: 0px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}
.admin-card {
  width: 25%;
  margin: 1rem;
  padding: 1rem 1rem;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  border-radius: 2rem;
  box-shadow: 5px 5px 10px #494949;
  transition: 1s ease;
  box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px,
    rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
}
.admin-card:hover {
  transform: scale(1.02);
}
.admin-card img {
  object-fit: contain;
  width: 80%;
  height: 150px;
  border: 2px solid #e1e3e1;
  border-radius: 2rem;
}
.admin-card h2 {
  font-size: 1.6rem;
  text-transform: capitalize;
  padding: 0.5rem 0;
  color: #0c0c0c;
}
.admin-price {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.4rem;
  text-transform: capitalize;
}
.flex p {
  font-size: 1.4rem;
  color: #000;
}

.my-form {
  width: 50%;
  margin: 0px auto;
}

.input-btn {
  text-align: center;
  width: 150px;
  position: absolute;
  right: 0;
  top: 0;
  border: none;
  padding: 1.2rem;
  background-color: #013bfc;
  color: #fff;
  outline: none;
  text-transform: capitalize;
  font-weight: 600;
  border-radius: 1rem;
  font-size: 1.7rem;
}
.input-btn:hover {
  background-color: #000000;
  color: #ffffff;
}
.table {
  width: 90%;
  margin: 2rem auto;
  background-color: #fff;
}
tr,
th,
td {
  border: 2px solid rgb(184, 184, 184);
  padding: 1rem;
}
thead {
  color: red;
  font-size: 2rem;
  font-weight: 600;
}
tbody {
  color: rgb(17, 17, 17);
  font-size: 1.8rem;
}
th:nth-of-type(2) {
  width: 30rem;
}
.table-btn {
  color: rgb(255, 255, 255);
  padding: 1rem;
  margin: 0 3rem;
  text-transform: capitalize;
  font-size: 1.8rem;
  border: none;
  outline: none;
  border-radius: 1rem;
}
.edit {
  background-color: green;
}
.delete {
  background-color: red;
}
.update {
  background-color: #013bfc;
  margin: 1rem 0;
}

@media only screen and (max-width: 1199px) {
  .detail-section {
    flex-direction: column;
  }
  .detail-image {
    display: none;
  }
  .admin-right-section {
    width: 40%;
  }
}
@media only screen and (max-width: 999px) {
  .detail-section {
    flex-direction: column;
  }
  .detail-image {
    display: none;
  }
  .admin-right-section {
    width: 60%;
  }
  .admin-form-section {
    width: 70%;
  }
  .admin-input {
    width: 80%;
  }
  .admin-card {
    width: 30%;
  }
}
@media only screen and (max-width: 799px) {
  .detail-section {
    flex-direction: column;
  }
  .detail-image {
    display: none;
  }
  .admin-right-section {
    width: 70%;
  }
  .admin-form-section {
    width: 80%;
  }
  .admin-input {
    width: 80%;
  }
  .admin-card {
    width: 45%;
  }
}
@media only screen and (max-width: 599px) {
  .admin-ul {
    width: 100%;
    flex-wrap: wrap;
    gap: 4rem;
  }
  .detail-image {
    display: none;
  }
  .admin-right-section {
    width: 90%;
  }
  .admin-form-section {
    width: 80%;
  }
  .admin-input {
    width: 80%;
  }
  .admin-card {
    width: 45%;
  }
}
