.user-nav {
  width: 90%;
  margin: 0px auto;
  padding: 0rem;
}
.user-ul {
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 2rem 0;
  background-color: #fff;
  border-radius: 1rem;
  text-transform: capitalize;
  box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px,
    rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px,
    rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
}
/* admin dash board css */
.user-left-section {
  width: 90%;
  margin: 2rem auto;
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  justify-content: space-between;
}
.user-detail-image {
  width: 100%;
  margin: 0rem auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.user-detail-image img {
  width: 90%;
  border-radius: 2rem;
  box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px,
    rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px,
    rgba(240, 46, 170, 0.05) 25px 25px;
}
.user-right-section {
  width: 90%;
  margin: 0rem auto;
  gap: 1rem;
  border: 2px solid #d1d1d1;
  border-radius: 2rem;
  padding: 1rem 0 2rem 0;
  box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px,
    rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
}

.user-right-section-details {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}
.user-right-section-details img {
  width: 50%;
  margin: 0 auto;
  text-align: center;
}
.user-right-section-details h2 {
  font-style: 1.6rem;
  margin: 2rem 0;
  color: blue;
  font-weight: 600;
}
.user-right-section-details h2 span {
  font-style: 1.4rem;
  margin: 1rem;
  color: red;
  font-weight: 500;
}

/* user profile */

.user-container {
  width: 100%;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f2f2;
}
.user-section {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0rem auto;
  border-radius: 2rem;
}
.user-image {
  width: 50%;
  margin: 2rem auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.user-image img {
  width: 65%;
  border-radius: 2rem;
  box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px,
    rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px,
    rgba(240, 46, 170, 0.05) 25px 25px;
}
.user-form-section {
  border: 2px solid #e1e1e1;
  margin: 0rem auto;
  padding: 1rem 0 2rem 0;
  border-radius: 2rem;
  width: 40%;
  box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px,
    rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
}

.user-form {
  width: 80%;
  margin: 0px auto;
}

.user-input {
  width: 100%;
  margin: 1rem auto;
}
.user-input label {
  font-size: 1.5rem;
  width: 100%;
}
.user-in {
  padding: 1rem;
  width: 100%;
  border: none;
  border: 2px solid #e1e1e1;
  border-radius: 1rem;
  outline: none;
  font-size: 1.8rem;
}

.user-btn {
  text-align: center;
  max-width: fit-content;
  margin: 1rem auto;
  border: none;
  padding: 1rem 2rem;
  background-color: #035afc;
  color: #fff;
  border-radius: 1rem;
  text-transform: capitalize;
  font-size: 1.5rem;
}
.user-btn:hover {
  background-color: #6197fc;
}

@media only screen and (max-width: 1199px) {
  .user-ul {
    width: 100%;
    flex-wrap: wrap;
    gap: 4rem;
  }
  .user-detail-image {
    display: none;
  }
  .user-right-section {
    width: 40%;
  }
  /* user profile */
  .user-image {
    display: none;
  }
  .user-section {
    flex-direction: column;
  }
  .user-form-section {
    width: 40%;
    margin: 0 auto;
  }
}
@media only screen and (max-width: 999px) {
  .user-ul {
    width: 100%;
    flex-wrap: wrap;
    gap: 4rem;
  }
  .usr-detail-image {
    display: none;
  }
  .user-right-section {
    width: 60%;
  }
  /* user pofile */
  .user-image {
    display: none;
  }
  .user-section {
    flex-direction: column;
  }
  .user-form-section {
    width: 60%;
    margin: 0 auto;
  }
}
@media only screen and (max-width: 799px) {
  .user-ul {
    width: 100%;
    flex-wrap: wrap;
    gap: 4rem;
  }
  .user-detail-image {
    display: none;
  }
  .user-right-section {
    width: 70%;
  }
  /* user profile */
  .user-image {
    display: none;
  }
  .user-section {
    flex-direction: column;
  }
  .user-form-section {
    width: 80%;
    margin: 0 auto;
    padding: 2rem 0;
  }
}
@media only screen and (max-width: 599px) {
  .user-ul {
    width: 100%;
    flex-wrap: wrap;
    gap: 4rem;
  }
  .user-detail-image {
    display: none;
  }
  .user-right-section {
    width: 90%;
  }
  /* user profile */
  .user-image {
    display: none;
  }

  .user-section {
    flex-direction: column;
  }
  .user-form-section {
    width: 100%;
    padding: 2rem 0;
  }
}
