.footer {
  width: 100%;
  margin: 0px auto;
  display: flex;
  align-items: center;
  gap: 4rem;
  justify-content: space-evenly;
  background-color: #000;
  color: #fff;
}
.foot {
  width: 30%;
  height: 300px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  box-shadow: 1px 2px 10px #fff;
  border-radius: 1rem;
  margin: 2rem 0;
  text-align: center;
}
.white {
  color: #fff !important;
}
li {
  display: flex;
  align-items: center;
  justify-content: center;
}
.foot iframe {
  width: 90%;
  display: flex;
  align-items: center;
  margin: 1rem auto;
  height: 150px;
  border-radius: 2rem;
}
.foot-link {
  margin: 0.5rem 0;
  color: #fff;
  font-size: 1.6rem;
  padding: 0.5rem 0;
  margin-left: 1rem;
}
.foot-para {
  color: #fff;
  background-color: blue;
  padding: 1rem 0;
  font-size: 1.5rem;
  text-align: center;
  word-spacing: 0.5rem;
  margin-bottom: 0;
}

@media only screen and (max-width: 999px) {
  .footer {
    flex-direction: column;
  }
  .foot {
    width: 80%;
  }
}
@media only screen and (max-width: 799px) {
  .footer {
    flex-direction: column;
  }
  .foot {
    width: 80%;
  }
}
@media only screen and (max-width: 599px) {
  .footer {
    flex-direction: column;
  }
  .foot {
    width: 80%;
  }
}
